import React from "react";
import Home from "./pages/home/home";
import Search from "./pages/search/search";
import { store } from "./app/store";
import { Provider } from "react-redux";
import { ThemeProvider } from "styled-components";
import { Router, Route, Switch } from "react-router";
import { createBrowserHistory } from "history";
import "ar-poncho/dist/css/poncho.min.css";
import "../src/styles/css/icono-arg.css";
import Header from "./components/Header/Header";
import Footer from "./components/Footer/Footer";
import Section from "./components/Section/Section";
import Nav from "./components/Nav/Nav";
import { PersistGate } from "redux-persist/integration/react";
import { persistStore } from "redux-persist";
import Results from "./pages/results/results";
import "./App.css";

const history = createBrowserHistory();

let persistor = persistStore(store);

const theme = {
  //basics
  text: "#333",
  secondaryText: "#525252",
  greyArea: "#f2f2f2",
  backgroundLightBlue: "#37bbed",
  visitedLink: "#6a1b99",
  border: "#ddd",
  //buttons, alerts, errors
  information: "#0072bb",
  success: "#2e7d33",
  danger: "#c62828",
  alert: "#f9a822",
  secondaryInformation: "#2897d4",
  //background or figures without text
  fucsia: "#ec407a",
  arandano: "#c2185b",
  grape: "#6a1b99",
  sky: "#039be5",
  greeny: "#6ea100",
  lime: "#cddc39",
  corn: "#ffce00",
  tomato: "#ef5350",
  darkOrange: "#EF6C00",
  blueishGreen: "#008388",
  //background or icons, no text
  escarapela: "##2cb9ee",
  lavander: "#9284be",
  tangerine: "#f79525",
  avocado: "#50b7b2",
  cherry: "#ed3d8f",
  lemon: "#d7df23",
};

function App() {
  return (
    <Provider store={store}>
      <PersistGate loading={null} persistor={persistor}>
      <ThemeProvider theme={theme}>
        <Router history={history}>
          <Header />
          <Nav />
          <Section>
          <Switch>
            <Route exact path="/busqueda" component={Search}/>
            <Route exact path="/resultados" component={Results} />
            <Route exact path="/" component={Home} />
          </Switch>
          </Section>
          <Footer />
        </Router>
      </ThemeProvider>
      </PersistGate>
    </Provider>
  );
}

export default App;
