import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import {
  fetchOrigin,
  fetchUnit,
  fetchTopic,
  fetchSubTopic,
} from "./SearchFormAPI";

const initialState = {
  status: "idle",
  origin: [],
  originLoaded: false,
  unit: [],
  unitLoaded: false,
  topic: [],
  topicLoaded: false,
  subTopic: [],
  subTopicLoaded: false,
  formData: {
    origin: "",
    unit: "",
    topic: "",
    subTopic: "",
    dateRange: {
      since: "",
      until: "",
    },
    numberRange: {
      from: "",
      to: "",
    },
  },
};

export const obtainOrigin = createAsyncThunk(
  "search/obtainOrigin",
  async () => {
    const response = await fetchOrigin();
    return response.data;
  }
);

export const obtainUnit = createAsyncThunk("search/obtainUnit", async () => {
  const response = await fetchUnit();
  return response.data;
});

export const obtainTopic = createAsyncThunk(
  "search/obtainTopic",
  async (id) => {
    const response = await fetchTopic(id);
    return response.data;
  }
);

export const obtainSubTopic = createAsyncThunk(
  "search/obtainSubTopic",
  async (id) => {
    const response = await fetchSubTopic(id);
    return response.data;
  }
);

export const searchSlice = createSlice({
  name: "search",
  initialState,
  reducers: {

    selectedOrigin: (state, action) => {
      state.formData.origin = action.payload;
    },
    selectedUnit: (state, action) => {
        state.formData.unit = action.payload;
    },
    selectedTopic: (state, action) => {
        state.formData.topic = action.payload;
    },
    selectedSubTopic: (state, action) => {
        state.formData.subTopic = action.payload;
    },
    selectedDateRangeSince: (state, action) => {
        state.formData.dateRange.since = action.payload;
    },
    selectedDateRangeUntil: (state, action) => {
      state.formData.dateRange.until = action.payload;
    },
    selectedNumberRangeFrom: (state, action) => {
      state.formData.numberRange.from = action.payload;
    },
    selectedNumberRangeTo: (state, action) => {
      state.formData.numberRange.to = action.payload;
    },
  },

  extraReducers: (builder) => {
    builder
      .addCase(obtainOrigin.pending, (state) => {
        state.status = "loading";
        state.originLoaded = false;
      })
      .addCase(obtainOrigin.fulfilled, (state, action) => {
        state.status = "idle";
        state.origin = action.payload;
        state.originLoaded = true;
      })
      .addCase(obtainUnit.pending, (state) => {
        state.status = "loading";
        state.unitLoaded = false;
      })
      .addCase(obtainUnit.fulfilled, (state, action) => {
        state.status = "idle";
        state.unit = action.payload;
        state.unitLoaded = true;
      })
      .addCase(obtainTopic.pending, (state) => {
        state.status = "loading";
        state.topicLoaded = false;
      })
      .addCase(obtainTopic.fulfilled, (state, action) => {
        state.status = "idle";
        state.topic = action.payload;
        state.topicLoaded = true;
      })
      .addCase(obtainSubTopic.pending, (state) => {
        state.status = "loading";
        state.subTopicLoaded = false;
      })
      .addCase(obtainSubTopic.fulfilled, (state, action) => {
        state.status = "idle";
        state.subTopic = action.payload;
        state.subTopicLoaded = true;
      });
  },
});

export const {
  selectedOrigin,
  selectedUnit,
  selectedTopic,
  selectedSubTopic,
  selectedDateRangeSince,
  selectedDateRangeUntil,
  selectedNumberRangeFrom,
  selectedNumberRangeTo,
} = searchSlice.actions;

export const selectOrigin = (state) => state.search.origin;

export const selectUnit = (state) => state.search.unit;

export const selectTopic = (state) => state.search.topic;

export const selectSubTopic = (state) => state.search.subTopic;

export const selectOriginLoaded = (state) => state.search.originLoaded;

export const selectUnitLoaded = (state) => state.search.unitLoaded;

export const selectTopicLoaded = (state) => state.search.topicLoaded;

export const selectSubTopicLoaded = (state) => state.search.subTopicLoaded;

export const selectFormData = (state) => state.search.formData;

export default searchSlice.reducer;
