import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import { fetchResults } from "./ResultsAPI";

const initialState = {
  fetchedDocuments: [],
  documents: [],
  pagedDocuments: [],
  documentsLoaded: false,
  order: {
    field: "numero",
    order: "asc",
  },
  totalPages: 1,
  actualPage: 0,
  perPage: 10,
  currentCount: 10,
};

export const obtainDocuments = createAsyncThunk(
  "results/obtainDocuments",
  async () => {
    const response = await fetchResults();
    return response.data;
  }
);

function toDate(dateString) {
  var parts = dateString.split("/");
  var dt = new Date(
    parseInt(parts[2], 10),
    parseInt(parts[1], 10) - 1,
    parseInt(parts[0], 10)
  );
  return dt;
}

function sortAsc(arr, field) {
  if (field === "fecha") {
    return arr.sort(function (a, b) {
      return toDate(a[field]) - toDate(b[field]);
    });
  } else {
    return arr.sort(function (a, b) {
      if (a[field] > b[field]) {
        return 1;
      }
      if (b[field] > a[field]) {
        return -1;
      }
      return 0;
    });
  }
}

function sortDesc(arr, field) {
  if (field === "fecha") {
    return arr.sort(function (a, b) {
      return toDate(b[field]) - toDate(a[field]);
    });
  } else {
    return arr.sort(function (a, b) {
      if (a[field] > b[field]) {
        return -1;
      }
      if (b[field] > a[field]) {
        return 1;
      }
      return 0;
    });
  }
}

function showPage(state) {
  let intialPosition = state.perPage * state.actualPage;
  state.pagedDocuments = state.documents.slice(
    intialPosition,
    intialPosition + state.perPage
  );
}

function setCantPages(state) {
  let pages = state.documents.length / state.perPage;
  state.totalPages = pages % 1 === 0 ? pages : Math.trunc(pages) + 1;
}

export const resultsSlice = createSlice({
  name: "results",
  initialState,
  reducers: {
    filterByValue: (state, action) => {
      state.documents = state.fetchedDocuments.filter((document) => {
        let value = action.payload.value.toLowerCase();
        return (
          document.numero.toString().toLowerCase().includes(value) ||
          document.fecha.toLowerCase().includes(value) ||
          document.origen.toLowerCase().includes(value) ||
          document.tema.toLowerCase().includes(value) ||
          document.descripci√≥n.toLowerCase().includes(value) ||
          document.unidades.toLowerCase().includes(value)
        );
      });
      showPage(state);
      state.actualPage = 0;
      setCantPages(state);
    },
    sortBy: (state, action) => {
      state.documents =
        action.payload.order === "asc"
          ? sortAsc(state.documents, action.payload.field)
          : sortDesc(state.documents, action.payload.field);
      state.order = {
        field: action.payload.field,
        order: action.payload.order,
      };
      showPage(state);
    },
    toPage: (state, action) => {
      let page = action.payload.page;
      let intialPosition = state.perPage * page;
      state.pagedDocuments = state.documents.slice(
        intialPosition,
        intialPosition + state.perPage
      );
      state.actualPage = page;
    },
    nextPage: (state, action) => {
      if (state.totalPages > state.actualPage + 1) {
        let intialPosition = state.perPage * (state.actualPage + 1);
        state.pagedDocuments = state.documents.slice(
          intialPosition,
          intialPosition + state.perPage
        );
        state.actualPage += 1;
      }
    },
    previousPage: (state, action) => {
      if (state.actualPage - 1 >= 0) {
        let intialPosition = state.perPage * (state.actualPage - 1);
        state.pagedDocuments = state.documents.slice(
          intialPosition,
          intialPosition + state.perPage
        );
        state.actualPage -= 1;
      }
    },
    setPerPage: (state, action) => {
      state.perPage = action.payload.perPage;
      setCantPages(state);
      showPage(state);
    },
    reSearch: (state, action) => {
      state.documentsLoaded = false;
      state.fetchedDocuments = [];
      state.documents = [];
      state.pagedDocuments = [];
    },
  },

  extraReducers: (builder) => {
    builder
      .addCase(obtainDocuments.pending, (state) => {
        state.status = "loading";
        state.documentsLoaded = false;
      })
      .addCase(obtainDocuments.fulfilled, (state, action) => {
        state.status = "idle";
        state.fetchedDocuments = action.payload;
        state.documents = action.payload;
        setCantPages(state);
        showPage(state);
        state.documentsLoaded = true;
      });
  },
});

export const {
  filterByValue,
  sortBy,
  toPage,
  nextPage,
  previousPage,
  setPerPage,
  reSearch,
} = resultsSlice.actions;

export const selectDocuments = (state) => state.results.documents;

export const selectPagedDocuments = (state) => state.results.pagedDocuments;

export const selectDocumentsLoaded = (state) => state.results.documentsLoaded;

export const selectDocumentsOrder = (state) => state.results.order;

export const selectTotalPages = (state) => state.results.totalPages;

export const selectActualPage = (state) => state.results.actualPage;

export const globalFilterByValue = (value) => (dispatch, getState) => {
  const currentOrder = selectDocumentsOrder(getState());
  dispatch(filterByValue(value));
  dispatch(sortBy(currentOrder));
};

export default resultsSlice.reducer;
