import React, { useState } from "react";
import styled, { css } from "styled-components";
import { Link } from "react-router-dom";

import { faBars } from "@fortawesome/free-solid-svg-icons";
import { faTimes } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

import Container from "../Base/Container";
import ToggleAnimation from "../Base/ToggleAnimation";

const StyledNav = styled.div`
  background-color: ${(props) => props.theme.backgroundLightBlue};
  position: relative;
  @media (max-width: 850px) {
    height: auto;
    min-height: 50px;
    display: flex;
    width: 100%;
  }
`;

const StyledNavMenu = styled.ul`
  width: 100%;
  display: flex;
  align-items: center;
  height: 80px;
  margin: 0;
  padding-left: 0;
  transition: all 0.5s ease;
  &:last-child {
    padding-top: 0;
  }
  @media (max-width: 850px) {
    ${(props) =>
    props.open
      ? css`
            display: flex;
            transition: all 0.5s ease;
          `
      : css`
            display: none;
            transition: all 0.5s ease;
          `}
    height: auto;
    flex-direction: column;
    justify-content: center;
    padding: 10px 0 0 0;
  }
`;

const StyledNavLink = styled.li`
  color: #ffffff;
  list-style: none;
  font-size: 22px;
  padding: 10px 40px 10px 0;
  & a {
    color: #ffffff;
    text-decoration: none;
  }
  & a:hover {
    text-decoration: none;
    color: ${(props) => props.theme.text};
    transition: 0.4s;
  }
  @media (max-width: 850px) {
    font-size: 18px;
    padding: 10px 20px 10px 0;
  }
  @media (max-width: 650px) {
    font-size: 18px;
    padding: 10px 0;
  }
`;

const StyledNavHamb = styled.div`
  display: none;
  color: #ffffff;
  list-style: none;
  font-size: 22px;
  padding: 10px 40px 10px 0;
  @media (max-width: 850px) {
    display: block;
  }
`;

const Icon = styled(FontAwesomeIcon)`
  cursor: pointer;
  font-size: 30px;
  position: absolute;
  top: 10px;
  left: 30px;
  cursor: pointer;
  &:hover {
    color: ${(props) => props.theme.text};
    transition: 0.5s;
  }
  @media (max-width: 550px) {
    left: 10px;
  }
`;

const Nav = () => {
  const [openMenu, setOpenMenu] = useState(false);
  return (
    <StyledNav>
      <Container>
        <StyledNavHamb
          onClick={() => {
            setOpenMenu((prevOpenMenu) => !prevOpenMenu);
          }}
        >
          <ToggleAnimation data={openMenu} />
          {openMenu ? <Icon icon={faTimes} /> : <Icon icon={faBars} />}
        </StyledNavHamb>
        <>
          <StyledNavMenu open={openMenu}>
            <StyledNavLink>
              <Link to="/busqueda">
                <a>Consulta General</a>
              </Link>
            </StyledNavLink>
            <StyledNavLink>
              <a href="https://www.argentina.gob.ar/inta">
                <a>Institucional</a>
              </a>
            </StyledNavLink>
          </StyledNavMenu>
        </>
      </Container>
    </StyledNav>
  );
};

export default Nav;
