import React, { useEffect } from "react";
import { useHistory } from "react-router";
import { useSelector, useDispatch } from "react-redux";
import {
  obtainOrigin,
  selectOrigin,
  selectOriginLoaded,
  obtainUnit,
  selectUnit,
  selectUnitLoaded,
  obtainTopic,
  selectTopic,
  selectTopicLoaded,
  selectSubTopic,
  selectSubTopicLoaded,
  obtainSubTopic,
  selectedOrigin,
  selectedUnit,
  selectedTopic,
  selectedSubTopic,
  selectedDateRangeSince,
  selectedDateRangeUntil,
  selectedNumberRangeFrom,
  selectedNumberRangeTo,
  selectFormData,
} from "./SearchFormSlice";
import { reSearch } from "../ResultsTable/ResultsSlice";

function SearchForm() {
  const origin = useSelector(selectOrigin);
  const originLoaded = useSelector(selectOriginLoaded);
  const unit = useSelector(selectUnit);
  const unitLoaded = useSelector(selectUnitLoaded)
  const topics = useSelector(selectTopic);
  const topicLoaded = useSelector(selectTopicLoaded)
  const subTopics = useSelector(selectSubTopic);
  const subTopicLoaded = useSelector(selectSubTopicLoaded);
  const formData = useSelector(selectFormData);
  let history = useHistory();
  const dispatch = useDispatch();

  useEffect(() => {
    if (!originLoaded) {
      dispatch(obtainOrigin());
    }

    if(!unitLoaded){
      dispatch(obtainUnit());
    }
  });

  function setSelectedOrigin(value){
    dispatch(obtainTopic(value))
    dispatch(selectedOrigin(value));
  }
  
  function setSelectedTopic(value){
    dispatch(obtainSubTopic(value))
    dispatch(selectedTopic(value));
  }

  function search(event){
    history.push("/resultados")
    dispatch(reSearch())
  }

  return (
    <div className="container">
      <section>
        <div className="row">
          <div className="col-md-8 col-md-offset-2">
            <h1>Consulta General de la Base de Datos</h1>

            <hr />

            <form>
              <fieldset>
                <div className="row m-t-2">
                  <div className="col-md-4">
                    <div className="form-group item-form">
                      <label>Fuente/Origen</label>
                      <select
                        className="form-control"
                        onChange={e => setSelectedOrigin(e.target.value)}
                      >
                        {origin && origin.length > 0 && originLoaded ? origin.map(option => (
                          <option key={option.id} value={option.id}>
                            {option.text}
                          </option>
                        )): <option value="Todos" selected="">Obteniendo datos...</option>}

                      </select>
                    </div>
                  </div>

                  <div className="col-md-8">
                    <div className="form-group item-form">
                      <label>Unidades</label>
                      <select
                        className="form-control"
                        onChange={e => dispatch(selectedUnit(e.target.value))}
                      >
                        {unit && unit.length > 0 && unitLoaded ? unit.map(option => (
                          <option key={option.id} value={option.id}>
                            {option.text}
                          </option>
                        )): <option value="Todos" selected="">Obteniendo datos...</option>}
                      </select>
                    </div>
                  </div>
                </div>

                <div className="row">
                  <div className="col-md-12">
                    <div className="form-group item-form">
                      <label>Tema</label>
                      <select
                        className="form-control"
                        onChange={e => setSelectedTopic(e.target.value)}
                      >
                        {topics && topics.length > 0 && topicLoaded ? topics.map(option => (
                          <option key={option.id} value={option.id}>
                            {option.text}
                          </option>
                        )): <option value="Todos" selected="">TODOS</option>}
                      </select>
                    </div>
                  </div>
                </div>

                <div className="row">
                  <div className="col-md-12">
                    <div className="form-group item-form">
                      <label>Subtema</label>
                      <select
                        className="form-control"
                        onChange={e => dispatch(selectedSubTopic(e.target.value))}
                      >
                        {subTopics && subTopics.length > 0 && subTopicLoaded ? subTopics.map(option => (
                          <option key={option.id} value={option.id}>
                            {option.text}
                          </option>
                        )): <option value="Todos" selected="">TODOS</option>}
                      </select>
                    </div>
                  </div>
                </div>
                <div className="row">
                  <div className="col-md-6">
                    <label>Rango de fechas</label>
                  </div>
                </div>
                <div className="row">
                  <div className="form-group col-md-6 item-form">
                    <p className="help-block">Desde</p>
                    <input
                      type="date"
                      className="form-control"
                      required=""
                      aria-required="true"
                      onChange={e => dispatch(selectedDateRangeSince(e.target.value))}
                    />
                  </div>
                  <div className="form-group col-md-6 item-form">
                    <p className="help-block">Hasta</p>
                    <input
                      type="date"
                      className="form-control"
                      required=""
                      aria-required="true"
                      onChange={e =>  dispatch(selectedDateRangeUntil(e.target.value))}
                    />
                  </div>
                </div>

                <div className="row">
                  <div className="col-md-6">
                    <label>Rango de Numeraci√≥n</label>
                  </div>
                </div>

                <div class="row">
                  <div class="col-md-6 form-group item-form">
                    <p className="help-block">Desde</p>
                    <input
                      type="number"
                      min="1"
                      class="form-control"
                      required=""
                      aria-required="true"
                      onChange={e => dispatch(selectedNumberRangeFrom(e.target.value))}
                    />
                  </div>
                  <div class="col-md-6 form-group item-form">
                    <p className="help-block">Hasta</p>
                    <input
                      type="number"
                      min="1"
                      class="form-control"
                      required=""
                      aria-required="true"
                      onChange={e => dispatch(selectedNumberRangeTo(e.target.value))}
                    />
                  </div>
                </div>

                <div className="row">
                  <div className="col-md-8">
                      <button type="button" className="btn btn-success" onClick={e => search(e)}>
                        Consultar
                      </button>
                  </div>
                </div>
              </fieldset>
            </form>
          </div>
        </div>
      </section>
    </div>
  );
}

export default SearchForm;
