import React, { useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import Loading from "../Loading/Loading";
import {
  selectDocuments,
  selectDocumentsLoaded,
  obtainDocuments,
  globalFilterByValue,
  sortBy,
  selectTotalPages,
  selectActualPage,
  toPage,
  nextPage,
  previousPage,
  selectPagedDocuments,
  setPerPage,
} from "./ResultsSlice";
import "./Results.css";

function ResultsTable() {
  const documents = useSelector(selectDocuments);
  const documentsOnPageSelected = useSelector(selectPagedDocuments);
  const documentsLoaded = useSelector(selectDocumentsLoaded);
  const totalPages = useSelector(selectTotalPages);
  const actualPage = useSelector(selectActualPage);
  const dispatch = useDispatch();
  const fields = [
    "Numero",
    "Fecha",
    "Origen",
    "Descripci√≥n",
    "Tema",
    "Unidades",
    "Documentos",
  ];

  function filterByInput(e) {
    let input = e.target.value;
    dispatch(globalFilterByValue({ value: input }));
  }

  function revertClassName(excludedField) {
    fields.map((field) =>
      field !== excludedField
        ? document.getElementById(field)
          ? (document.getElementById(field).className = "sorting")
          : null
        : null
    );
  }

  function setOrder(value, field) {
    if (
      value.target.className === "sorting" ||
      value.target.className === "sorting_asc"
    ) {
      value.target.className = "sorting_desc";
      dispatch(sortBy({ order: "desc", field: field.toLowerCase() }));
    } else {
      value.target.className = "sorting_asc";
      dispatch(sortBy({ order: "asc", field: field.toLowerCase() }));
    }
    revertClassName(field);
  }

  useEffect(() => {
    if (!documentsLoaded) {
      dispatch(obtainDocuments());
      window.scrollTo(0, 0)
    }
  });

  function toTop(){
    window.scrollTo(0, 200);
  }

  function goPage(value){
    dispatch(toPage({ page: value }));
    toTop();
  }

  function toPreviousPage(){
    dispatch(previousPage());
    toTop();
  }

  function toNextPage(){
    dispatch(nextPage())
    toTop();
  }

  if (!documentsLoaded) {
    return <Loading />;
  }

  return (
    <div className="container">
      <section>
        <div className="row m-t-2">
          <div className="col-sm-12 col-lg-6" id="ponchoTableSearchCont">
            <div className="form-group">
              <label for="ponchoTableSearch">Busc√° por palabra clave</label>
              <input
                className="form-control"
                id="ponchoTableSearch"
                type="text"
                onChange={(e) => filterByInput(e)}
              />
            </div>
          </div>
          <div class="col-md-2">
            <div class="form-group item-form">
              <label>Resultados por pagina</label>
              <select onChange={e=>dispatch(setPerPage({perPage : e.target.value}))} class="form-control">
                <option value="10">10</option>
                <option value="20">20</option>
                <option value="30">30</option>
                <option value="50">50</option>
              </select>
            </div>
          </div>
        </div>
        <div className="row">
          <div className="col-md-12 m-b-4">
            <div
              id="ponchoTable_wrapper"
              className="dataTables_wrapper form-inline dt-bootstrap no-footer"
            >
              <div className="row">
                <div className="col-sm-12">
                  <div
                    className="dataTables_info"
                    id="tableFiltro_info"
                    role="status"
                    aria-live="polite"
                  >
                    {documents.length} resultados
                  </div>
                </div>
              </div>
              <div className="row">
                <div className="col-sm-12">
                  <table
                    className="table table-responsive-poncho table-striped dataTable no-footer"
                    id="tableFiltro"
                    aria-describedby="tableFiltro_info"
                    role="grid"
                  >
                    <thead>
                      <tr role="row">
                        {fields.map((field) =>
                          field !== "Documentos" ? (
                            <th
                              key={field}
                              id={field}
                              className="sorting"
                              onClick={(e) => setOrder(e, field)}
                            >
                              {field}
                            </th>
                          ) : (
                            <th key={field}>{field}</th>
                          )
                        )}
                      </tr>
                    </thead>
                    <tbody>
                      {documents && documents.length > 0 && documentsLoaded
                        ? documentsOnPageSelected.map((option) => (
                            <tr key={option.id} role="row">
                              <td className="">{option.numero}</td>
                              <td className="">{option.fecha}</td>
                              <td className="">{option.origen}</td>
                              <td className="">{option.descripci√≥n}</td>
                              <td className="">{option.tema}</td>
                              <td className="">{option.unidades}</td>
                              <td className="">
                                <a href={option.documentos}>Ver PDF</a>
                              </td>
                            </tr>
                          ))
                        : null}
                    </tbody>
                  </table>
                </div>
              </div>
              <div className="row">
                <div className="col-md-12 text-center flex-center">
                  <div className="dataTables_paginate paging_simple_numbers">
                    <ul className="pagination">
                      <li
                        className={
                          "paginate_button previous" +
                          (actualPage - 1 >= 0 ? "" : " disabled")
                        }
                      >
                        <a onClick={(e) => toPreviousPage()}>&lt;</a>
                      </li>
                      {Array.from(Array(totalPages).keys()).map((value) => (
                        <li
                          id={value + 1}
                          className={
                            "paginate_button" +
                            (value === actualPage ? " active" : "")
                          }
                        >
                          <a onClick={(e) => goPage(value)}>
                            {value + 1}
                          </a>
                        </li>
                      ))}
                      <li
                        className={
                          "paginate_button next" +
                          (actualPage + 1 >= totalPages ? " disabled" : "")
                        }
                      >
                        <a onClick={(e) => toNextPage()}>&gt;</a>
                      </li>
                    </ul>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
    </div>
  );
}

export default ResultsTable;
