export function fetchCount(amount = 1) {
    return new Promise((resolve) =>
      setTimeout(() => resolve({ data: amount }), 500)
    );
}

export function fetchOrigin() {
    return new Promise((resolve) =>
      fetch("/api/origin")
        .then((res) => res.json())
        .then((json) => {
          console.log("json!", json);
          resolve({ data: json.origins });
        })
    );
}

