export function fetchTopic(id) {
  return new Promise((resolve) =>
    fetch(`/api/origin/${id}/topics`)
      .then((res) => res.json())
      .then((json) => {
        console.log("json!", json);
        resolve({ data: json.topics });
      })
  );
}

