
export function fetchCount(amount = 1) {
  return new Promise((resolve) =>
    setTimeout(() => resolve({ data: amount }), 500)
  );
}

export function fetchUsers() {
  return new Promise((resolve) =>
    fetch("/api/users")
      .then((res) => res.json())
      .then((json) => {
        resolve({ data: json.users });
      })
  );
}
